@charset "UTF-8";
.icon-arrow-right { width: 1em; height: 1em; fill: initial; }

html { box-sizing: border-box; }

*, *::before, *::after { box-sizing: inherit; }

:root { font-family: "Roboto", sans-serif; line-height: 1.6; }

:root * { -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; -webkit-overflow-scrolling: touch; }

html, body { height: 100%; font-size: 30px; }

@media (max-width: 1900px) { html, body { font-size: calc(17.4px + 0.66316vw) ; } }

.wrapper { width: 100%; font-family: "Roboto", sans-serif; font-size: 1rem; }

a { color: #fff; }

a:hover { text-decoration: none; }

picture source, picture img { width: 100%; height: 100%; object-fit: cover; object-position: center; }

.bg-absolute { position: absolute; top: 0; left: 0; width: 100%; height: 100%; z-index: -1; background-color: #ff4000; }

.bg-absolute img { display: block; width: 100%; height: 100%; object-fit: cover; object-position: center bottom; }

img { border: 0; }

img:not([src]):not([srcset]) { visibility: hidden; }

.lazyload { animation-name: fade; animation-duration: 1s; }

@keyframes fade { from { opacity: 0; }
  to { opacity: 1; } }

.underlined { position: relative; transition: color 0.2s ease; }

.underlined:after { content: ''; position: absolute; left: 0; bottom: 1px; width: 100%; height: 1px; background-color: #fff; transition: background-color 0.2s ease; }

.flip-x { display: flex; align-items: center; justify-content: center; transform: scaleX(-1); filter: FlipH; -ms-filter: 'FlipH'; }

ul li { list-style-type: none; }

.icon { fill: #fff; max-width: 100vw; transition: all 0.2s ease; }

@media only screen and (max-width: 480px) { .hidden-xs-down { display: none; }
  .btn { font-size: 12px; } }

@media only screen and (min-width: 480px) { .hidden-xs-up { display: none; } }

.header { height: 4.25rem; border: solid 1px #707070; border-bottom: 0px; }

