@media only screen and (max-width: 768px) { .btn--view { margin-left: 1.8rem; } }

@media only screen and (max-width: 480px) { .btn--view { position: absolute; right: 0; top: 100%; margin: 0; transform: translateY(0); } }

.btn--view .icon { margin-left: 1rem; }

/*begin hamburger*/
#nav-icon1 { width: 1.3rem; height: 16px; position: relative; transform: rotate(0deg); transition: 0.4s ease-in-out; cursor: pointer; }

#nav-icon1 span { display: block; position: absolute; height: 2px; width: 100%; background: #ff4000; opacity: 1; left: 0; transform: rotate(0deg); transition: 0.25s ease-in-out; }

#nav-icon1 span:nth-child(1) { top: 0; }

#nav-icon1 span:nth-child(2) { top: 8px; display: none; }

#nav-icon1 span:nth-child(3) { top: 12px; }

.navbar-toggler[aria-expanded='true'] #nav-icon1 { max-width: 27px; }

.navbar-toggler[aria-expanded='true'] #nav-icon1 span:nth-child(1) { top: 5px; transform: rotate(135deg); }

.navbar-toggler[aria-expanded='true'] #nav-icon1 span:nth-child(2) { opacity: 0; left: -60px; }

.navbar-toggler[aria-expanded='true'] #nav-icon1 span:nth-child(3) { top: 5px; transform: rotate(-135deg); }

#touch-menu { z-index: 10; }

/*end hamburger*/
